import {
  deviceGroupGetTreeFunc,
  deviceWorkshopTypeGetTreeFunc
} from '@/utils/publicFunction';
// 导入所需接口
import {
  GetPageWarningUnprocessed,
  GetPageWarningProcessed
} from '@/api/equipmentstatus';
import { exportWarningProcessed, exportWarningUnprocessed } from '@/api/export';
export default {
  components: {
    // 加载通用table组件
    VXETable
  },
  props: {
    groupType: String,
    active2: Number,
    showInfo: {
      type: Function,
      default: null
    }
  },
  data() {
    return {
      EQUIPMENT_LIVE: EQUIPMENT_LIVE,
      BTN_TEXT: BTN_TEXT,
      // 可编辑配
      editConfig: {
        trigger: 'click',
        mode: 'row',
        showStatus: false
        // icon: 'fa fa-file-text-o'
      },
      showFlag: 1,
      searchData: [],
      // 显示那个表
      showTable: true,
      deviceName: '',
      // table是否加载
      tableLoadings: false,
      tableLoading: false,
      // 打印数据
      outData: [],
      // table数据
      tableDatas: [],
      tableData: [],
      // table表头配置
      tableColumn: [
        {
          field: 'factory',
          title: '工厂',
          width: '',
          align: 'center'
        },
        {
          field: 'workshop',
          title: '车间',
          width: '',
          align: 'center'
        },
        {
          field: 'productionLine',
          title: '产线',
          width: '',
          align: 'center'
        },
        {
          field: 'deviceName',
          title: EQUIPMENT_LIVE.DEVICE_NAME,
          width: '',
          align: 'center',
          sortable: true
        },
        {
          field: 'typeName',
          title: EQUIPMENT_LIVE.DEVICE_TYPE,
          width: '',
          align: 'center',
          sortable: true
        },
        {
          field: 'warningStartTime',
          title: EQUIPMENT_LIVE.ALARMS_TIME,
          formatter: ['formatDate', 'yyyy-MM-dd hh:mm:ss'],
          width: '200',
          align: 'center'
        },
        {
          field: 'warnName',
          title: EQUIPMENT_LIVE.ALARMS_NAME,
          width: '',
          align: 'center'
        },
        {
          field: 'warnCode',
          title: EQUIPMENT_LIVE.ALARMS_NUMBER,
          width: '',
          align: 'center'
        },
        {
          field: 'warnDetail',
          title: EQUIPMENT_LIVE.ALARMS_INFORMATION,
          width: '',
          align: 'center'
        },
        {
          field: 'warnRemark',
          title: EQUIPMENT_LIVE.REMARKS,
          width: '',
          align: 'center'
        }
      ],
      tableColumns: [
        {
          field: 'factory',
          title: '工厂',
          width: '',
          align: 'center'
        },
        {
          field: 'workshop',
          title: '车间',
          width: '',
          align: 'center'
        },
        {
          field: 'productionLine',
          title: '产线',
          width: '',
          align: 'center'
        },
        {
          field: 'deviceName',
          title: EQUIPMENT_LIVE.DEVICE_NAME,
          width: '',
          align: 'center',
          sortable: true
        },
        {
          field: 'typeName',
          title: EQUIPMENT_LIVE.DEVICE_TYPE,
          width: '',
          align: 'center',
          sortable: true
        },
        {
          field: 'warningCount',
          title: EQUIPMENT_LIVE.ALARMS_ALL,
          width: '',
          align: 'center'
        },
        {
          field: 'lastWarningName',
          title: EQUIPMENT_LIVE.PRE_ALARM_NAME,
          width: '',
          align: 'center'
        },
        {
          field: 'warningStartTime',
          title: EQUIPMENT_LIVE.ALARMS_START_TIME,
          formatter: ['formatDate', 'yyyy-MM-dd HH:mm:ss'],
          width: '200',
          align: 'center'
        },
        {
          field: 'warningContinuedTime',
          title: EQUIPMENT_LIVE.ALARMS_CONTINUED_TIME,
          // formatter: 'timeStampSeconds',
          width: '',
          align: 'center'
        },
        // {
        //   field: 'warningEndTime',
        //   title: EQUIPMENT_LIVE.ALARMS_FINISH_TIME,
        //   formatter: ['formatDate', 'yyyy-MM-dd HH:mm:ss'],
        //   width: '200',
        //   align: 'center',
        //   sortable: true
        // },
        {
          field: '',
          title: BTN_TEXT.OPERATION,
          width: '',
          align: 'center',
          type: 'html',
          slots: {
            default: ({ row, column, cellValue }) => {
              return [
                <div>
                  <el-button
                    type="text"
                    onClick={() => this.toInfo(row, column, true)}
                  >
                    {EQUIPMENT_LIVE.ALARM_HISTORY}
                  </el-button>
                </div>
              ];
            }
          }
        }
      ],
      typeOptions: [],
      groupOptions: [],
      typeValue: [],
      orderByData: {},
      groupValue: [],
      // table分页配置
      tablePages: {
        total: 0,
        currentPage: 1,
        pageSize: 10,
        align: 'right',
        pageSizes: [10, 20, 50, 100, 200, 500],
        layouts: [
          'Sizes',
          'PrevJump',
          'PrevPage',
          'Number',
          'NextPage',
          'NextJump',
          'FullJump',
          'Total'
        ],
        perfect: true
      },
      tablePage: {
        total: 0,
        currentPage: 1,
        pageSize: 10,
        align: 'right',
        pageSizes: [10, 20, 50, 100, 200, 500],
        layouts: [
          'Sizes',
          'PrevJump',
          'PrevPage',
          'Number',
          'NextPage',
          'NextJump',
          'FullJump',
          'Total'
        ],
        perfect: true
      },
      active: 0,
      tabs: [
        {
          type: 'process',
          name: '未处理',
          value: '0'
        },
        {
          type: 'Processed',
          name: '已处理',
          value: '1'
        }
      ]
    };
  },
  watch: {
    active2(val) {
      this.toggle(val);
    }
  },
  created() {
    // 调用列表查询接口
    // this.findList();
    // 类型下拉数据
    this.getTypeTree();
    // 车间分组下拉数据
    this.getGroupTree();
    this.getWarningUnprocessed();
    this.showTable = true;
  },
  methods: {
    /**
     * @description:获取未处理表格数据
     * @param
     * @return:
     * @author: CH
     */
    async getWarningUnprocessed() {
      try {
        this.tableLoading = true;

        const res = await GetPageWarningUnprocessed(
          {
            size: this.tablePage.pageSize,
            current: this.tablePage.currentPage
          },
          {
            conditionJson1: this.searchData.conditionArr1,
            operator: this.searchData.groupOperator,
            conditionJson2: this.searchData.conditionArr2,
            orderBy: this.orderByData
          }
        );
        if (res.code === STATUS_CODE.OPERATION_SUCCESS) {
          // 将请求数据写入table
          this.tableData = res.rows;
          // 将总条数写入table分页配置
          this.tablePage.total = res.total;
          // 加载动画消失
          this.tableLoading = false;
        } else {
          // 提示异常信息
          showWarningMessage(res.message);
        }
      } catch (error) {
        showErrorNotify(TITLE.NOTIFY, error);
      } finally {
      }
    },

    /**
     * @description: 执行table分页
     * @param currentPage 第几页
     * @param pageSize 一页几条
     * @return: 无
     * @author: CH
     */
    onTablePageChange(currentPage, pageSize) {
      // 点击页数写入table分页配置
      this.tablePage.currentPage = currentPage;
      // 一页条数夜路table分页配置
      this.tablePage.pageSize = pageSize;
      // 调用查询接口
      this.getWarningUnprocessed();
    },
    /**
     * @description:获取表格数据
     * @param
     * @return:
     * @author: CH
     */
    async GetWarningProcessed() {
      try {
        this.tableLoadings = true;
        const res = await GetPageWarningProcessed(
          {
            size: this.tablePages.pageSize,
            current: this.tablePages.currentPage
          },
          {
            conditionJson1: this.searchData.conditionArr1,
            operator: this.searchData.groupOperator,
            conditionJson2: this.searchData.conditionArr2,
            orderBy: this.orderByData
          }
        );
        if (res.code === STATUS_CODE.OPERATION_SUCCESS) {
          // 将请求数据写入table
          this.tableDatas = res.rows;
          // 将总条数写入table分页配置
          this.tablePages.total = res.total;
          // 加载动画消失
          this.tableLoadings = false;
        } else {
          // 提示异常信息
          showWarningMessage(res.message);
        }
      } catch (error) {
        showErrorNotify(TITLE.NOTIFY, error);
      } finally {
      }
    },

    /**
     * @description: 执行table分页
     * @param currentPage 第几页
     * @param pageSize 一页几条
     * @return: 无
     * @author: CH
     */
    onTablePageChanges(currentPage, pageSize) {
      // 点击页数写入table分页配置
      this.tablePages.currentPage = currentPage;
      // 一页条数夜路table分页配置
      this.tablePages.pageSize = pageSize;
      // 调用查询接口
      this.GetWarningProcessed();
    },
    /**
     * @description: 获取类型树数据
     * @param obj ztree对象
     * @return: 无
     * @author: ZJM
     */
    async getGroupTree() {
      // 若是车间分组
      // 执行调用接口
      try {
        const res = await deviceGroupGetTreeFunc();
        if (res.code === this.GLOBAL.STATUS_CODE.OPERATION_SUCCESS) {
          this.groupOptions = res.rows;
        } else {
          // 提示异常信息
          showWarningMessage(res.message);
        }
      } catch (error) {
        showErrorNotify(this.GLOBAL.TITLE.NOTIFY, error);
      } finally {
      }
    },
    /**
     * @description: 获取设备类型树数据
     * @param obj ztree对象
     * @return: 无
     * @author: ZJM
     */
    async getTypeTree() {
      // 若是设备类型
      // 执行调用接口
      try {
        const res = await deviceWorkshopTypeGetTreeFunc();
        if (res.code === this.GLOBAL.STATUS_CODE.OPERATION_SUCCESS) {
          this.typeOptions = res.rows;
        } else {
          // 提示异常信息
          showWarningMessage(res.message);
        }
      } catch (error) {
        showErrorNotify(this.GLOBAL.TITLE.NOTIFY, error);
      } finally {
      }
    },
    /**
     * @description: 查询
     * @param obj ztree对象
     * @return: 无
     * @author: ZJM
     */
    search(data) {
      this.searchData = data;
      if (this.active === 0) {
        // this.getWarningUnprocessed();
        this.onTablePageChange(1, this.tablePage.pageSize);
        this.showTable = true;
      } else {
        this.onTablePageChanges(1, this.tablePages.pageSize);
        this.showTable = false;
      }
    },
    sortChange(property, order) {
      this.orderByData = {};
      this.orderByData[property] = order;
      if (this.active === 0) {
        // this.getWarningUnprocessed();
        this.onTablePageChange(1, this.tablePage.pageSize);
        this.showTable = true;
      } else {
        this.onTablePageChanges(1, this.tablePages.pageSize);
        this.showTable = false;
      }
    },
    /**
     * @description: 重置
     * @param {type}
     * @return:
     * @author: CH
     */
    reset() {
      this.typeValue = '';
      this.groupValue = '';
      this.deviceName = '';
      if (this.active === 0) {
        // this.getWarningUnprocessed();
        this.onTablePageChange(1, 10);
        this.showTable = true;
      } else {
        this.onTablePageChanges(1, 10);
        this.showTable = false;
      }
    },
    /**
     * @description: 切换tab事件
     * @param i 下标
     * @param v 组件名
     * @return: 无
     * @author: ZJM
     */
    toggle(i) {
      this.searchData = [];
      this.orderByData = {};
      this.active = i;
      // this.showFlag = v;
      if (this.active === 1) {
        // this.GetWarningProcessed();
        this.onTablePageChanges(1, 10);
        this.showTable = false;
      } else {
        this.onTablePageChange(1, 10);
        // this.getWarningUnprocessed();
        this.showTable = true;
      }
      // this.groupType = v;
    },
    /**
     * @description:显示告警明细
     * @param
     * @return:
     * @author: CH
     */
    toInfo(row, column) {
      this.showInfo(2, row);
    },
    /**
     * @description:导出判断
     * @param
     * @return:
     * @author: CH
     */
    exportTable() {
      if (this.active === 0) {
        this.outputWarningUnprocessed();
      } else {
        this.outputWarningProcessed();
      }
    },
    /**
     * @description:获取未处理表格数据
     * @param
     * @return:
     * @author: CH
     */
    async outputWarningUnprocessed() {
      try {
        const res = await exportWarningUnprocessed(
          {},
          {
            conditionJson1: this.searchData.conditionArr1,
            operator: this.searchData.groupOperator,
            conditionJson2: this.searchData.conditionArr2,
            orderBy: this.orderByData
          }
        );
        if (res.data.code !== undefined) {
          showWarningMessage(res.message);
        } else {
          this.$options.filters.setExportFunc(res, '故障告警未处理.xls');
        }
      } catch (error) {
        showErrorNotify(this.GLOBAL.TITLE.NOTIFY, error);
      } finally {
      }
    },
    /**
     * @description:获取表格数据
     * @param
     * @return:
     * @author: CH
     */
    async outputWarningProcessed() {
      try {
        const res = await exportWarningProcessed(
          {},
          {
            conditionJson1: this.searchData.conditionArr1,
            operator: this.searchData.groupOperator,
            conditionJson2: this.searchData.conditionArr2,
            orderBy: this.orderByData
          }
        );
        if (res.data.code !== undefined) {
          showWarningMessage(res.message);
        } else {
          this.$options.filters.setExportFunc(res, '故障告警已处理.xls');
        }
      } catch (error) {
        showErrorNotify(this.GLOBAL.TITLE.NOTIFY, error);
      } finally {
      }
    }
  }
};
