  <el-container>
    <div class="main" v-show="hideMain">
      <el-header>
        <header-buttons @buttonClickEvent="buttonClickEvent" cssType="1">
        </header-buttons>
        <!-- <HeaderTitle @searchButtonClick="showSearchCard" :flag="true">
          <div slot="head-operation" class="head-operation">
            <HeadOperationItem name="showInfo" @onOprationItem="exportTable()">
              <i slot="operation-icon" class="icon-export"></i>
              <span slot="operation-name">{{BTN_TEXT.EXPORT}}</span>
            </HeadOperationItem>
            <HeadOperationItem name="showInfo" @onOprationItem="refresh()">
