// 引入通用table组件
import VXETable from '@/components/common/Table.vue';
import warningTable from '@/views/equipment/AlarmMontoring/warningTable.vue';
import HeaderTitle from '@/components/common/PageNavBar/HeaderTitle';
import HeadOperationItem from '@/components/common/PageNavBar/HeadOperationItem';
import { showWarningMessage } from '@/utils/message';
import { showErrorNotify } from '@/utils/notify';
import { List } from 'vxe-table';
import {
  STATUS_CODE,
  TITLE,
  BTN_TEXT,
  EQUIPMENT_LIVE
} from '@/utils/constantsFile';
import { GetPageParameterWarningInfo } from '@/api/equipmentstatus';

export default {
  name: 'tabs',
  props: {
    deviceData: List,
    back: {
      type: Function,
      default: null
    }
  },
  data() {
    return {
      // tab默认激活下标
      BTN_TEXT: BTN_TEXT,
      EQUIPMENT_LIVE: EQUIPMENT_LIVE,
      title: '',
      // 可编辑配
      editConfig: {
        trigger: 'click',
        mode: 'row',
        showStatus: false
        // icon: 'fa fa-file-text-o'
      },
      // table是否加载
      tableLoadings: false,
      // 打印数据
      outData: [],
      // table数据
      tableDatas: [],
      // table表头配置
      tableColumns: [
        {
          field: 'warningStartTime',
          title: EQUIPMENT_LIVE.ALARMS_START_TIME,
          formatter: ['formatDate', 'yyyy-MM-dd HH:mm:ss'],
          width: '',
          align: 'center'
        },
        {
          field: 'warningEndTime',
          title: EQUIPMENT_LIVE.ALARMS_FINISH_TIME,
          formatter: ['formatDate', 'yyyy-MM-dd HH:mm:ss'],
          width: '',
          align: 'center'
        },
        {
          field: 'warningContinuedTime',
          title: EQUIPMENT_LIVE.ALARMS_CONTINUED_TIME,
          formatter: 'timeStampSeconds',
          width: '',
          align: 'center'
        },
        {
          field: 'warnName',
          title: EQUIPMENT_LIVE.ALARMS_NAME,
          width: '',
          align: 'center'
        },
        {
          field: 'warnValue',
          title: '实际值',
          width: '',
          align: 'center'
        },
        {
          field: 'upLine',
          title: '上限',
          width: '',
          align: 'center'
        },
        {
          field: 'downLine',
          title: '下限',
          width: '',
          align: 'center'
        }
      ],
      // table分页配置
      tablePages: {
        total: 0,
        currentPage: 1,
        pageSize: 10,
        align: 'right',
        pageSizes: [10, 20, 50, 100, 200, 500],
        layouts: [
          'Sizes',
          'PrevJump',
          'PrevPage',
          'Number',
          'NextPage',
          'NextJump',
          'FullJump',
          'Total'
        ],
        perfect: true
      }
    };
  },
  created() {
    this.title = EQUIPMENT_LIVE.ALARMS_INFO + '--' + this.deviceData.deviceName;
    this.findList();
    // 调用列表查询接口
  },
  methods: {
    /**
     * @description: 切换tab事件
     * @param i 下标
     * @param v 组件名
     * @return: 无
     * @author: ZJM
     */
    toggle(i, v) {
      this.active = i;
      this.currentView = v;
      // this.groupType = v;
    },
    /**
     * @description: 进入详情页面
     * @param obj ztree对象
     * @return: 无
     *@author: CH
     */
    backMain() {
      this.back();
    },
    /**
     * @description:获取表格数据
     * @param
     * @return:
     * @author: CH
     */
    async findList() {
      try {
        this.tableLoadings = true;
        const res = await GetPageParameterWarningInfo(
          {
            size: this.tablePages.pageSize,
            current: this.tablePages.currentPage,
            deviceCode: this.deviceData.deviceCode
          }
        );
        if (res.code === STATUS_CODE.OPERATION_SUCCESS) {
          // 将请求数据写入table
          this.tableDatas = res.rows;
          // 将总条数写入table分页配置
          this.tablePages.total = res.total;
          // 加载动画消失
          this.tableLoadings = false;
        } else {
          // 提示异常信息
          showWarningMessage(res.message);
        }
      } catch (error) {
        showErrorNotify(TITLE.NOTIFY, error);
      } finally {
      }
    },
    /**
     * @description: 执行table分页
     * @param currentPage 第几页
     * @param pageSize 一页几条
     * @return: 无
     * @author: CH
     */
    onTablePageChanges(currentPage, pageSize) {
      // 点击页数写入table分页配置
      this.tablePages.currentPage = currentPage;
      // 一页条数夜路table分页配置
      this.tablePages.pageSize = pageSize;
      // 调用查询接口
      this.findList();
    }
  },
  components: {
    // 加载未审核组件
    HeaderTitle,
    warningTable,
    HeadOperationItem,
    VXETable
  }
};
