// 引入组件
import warningInfo from '@/views/equipment/AlarmMontoring/warningInfo.vue';
import warningTable from '@/views/equipment/AlarmMontoring/warningTable.vue';
import parameterAlarmTable from '@/views/equipment/AlarmMontoring/parameterAlarmTable.vue';
import parameterAlarmInfo from '@/views/equipment/AlarmMontoring/parameterAlarmInfo.vue';
import HeaderTitle from '@/components/common/PageNavBar/HeaderTitle';
import {
  EQUIPMENT_LIVE,
  BTN_TEXT,
  STATUS_CODE,
  TITLE
} from '@/utils/constantsFile';
import HeadOperationItem from '@/components/common/PageNavBar/HeadOperationItem';
// 搜索框
import SearchBox from '@/components/common/SearchBox';
// 提示框
import { showWarningMessage } from '@/utils/message';
import { showErrorNotify } from '@/utils/notify';

import { GetListQueryParameterWarning } from '@/api/equipmentstatus';
import { ISANDON } from '@/utils/ajaxConfigure';
// // 引入审核通过组件
// import waterMeter from '@/views/main/EnergyAllocation/waterMeter.vue';
// // 引入审核未通过组件
// import gasMeter from '@/views/main/EnergyAllocation/gasMeter.vue';
export default {
  name: 'AlarmMontoring',
  data() {
    return {
      // 搜索框有关
      isSearchBoxShow: false,
      srearchList: [],
      searchData: [],
      // tab默认激活下标
      BTN_TEXT: BTN_TEXT,
      EQUIPMENT_LIVE: EQUIPMENT_LIVE,
      active: 0,
      active2: 0,
      // rows: {},
      showFlag: 0,
      deviceData: {},
      hideMain: true,
      // 默认引入的组件名称
      currentView: 'warningTable',
      dialogVisible: false,

      tabs: [
        {
          type: 'warningTable',
          name: '故障报警',
          views: 'warningTable'
        },
        ...(ISANDON ? [] : [{
          type: 'parameterAlarmTable',
          name: '参数预警',
          views: 'parameterAlarmTable'
        }])
      ],
      tab: [
        {
          type: 'process',
          name: '未处理',
          value: '0'
        },
        {
          type: 'Processed',
          name: '已处理',
          value: '1'
        }
      ]
    };
  },
  created() {
    // 调用列表查询接口
    this.GetListQueryParameterWarning();
  },
  methods: {
    /**
     * @description: （主页面）点击“搜索”按钮，打开搜索框
     * @param 无
     * @return: void
     * @author: cyb
     */
    showSearchCard() {
      // alert(1321);
      this.isSearchBoxShow = !this.isSearchBoxShow;
    },
    /**
     * @description: 切换tab事件
     * @param i 下标
     * @param v 组件名
     * @return: 无
     * @author: ZJM
     */
    toggle(i, v) {
      this.$refs.searchBox.resetCondtionGroup();
      this.active = i;
      this.currentView = v;
      this.active2 = 0;
      // this.groupType = v;
    },
    /**
     * @description: 切换tab事件
     * @param i 下标
     * @param v 组件名
     * @return: 无
     * @author: ZJM
     */
    toggle2(i, v) {
      this.$refs.searchBox.resetCondtionGroup();
      this.active2 = i;
      // this.groupType = v;
    },
    /**
     * @description:显示详情也卖弄
     * @param {type}
     * @return:
     * @author: CH
     */
    showInfo(showFlag, row) {
      this.showFlag = showFlag;
      this.deviceData = row;
      this.hideMain = false;
    },
    /**
     * @description:返回主页面
     * @param {type}
     * @return:
     * @author: CH
     */
    back() {
      this.showFlag = 0;
      this.hideMain = true;
    },
    /**
     * @description:导出
     * @param {type}
     * @return:
     * @author: CH
     */
    exportTable() {
      if (this.active === 1) {
        this.$refs.parameterAlarmTable.exportTable();
      } else {
        this.$refs.warningTable.exportTable();
      }
    },
    /**
     * @description:获取表格数据
     * @param
     * @return:
     * @author: CH
     */
    async GetListQueryParameterWarning() {
      try {
        const res = await GetListQueryParameterWarning({});
        if (res.code === STATUS_CODE.OPERATION_SUCCESS) {
          this.srearchList = res.rows;
        } else {
          // 提示异常信息
          showWarningMessage(res.message);
        }
      } catch (error) {
        showErrorNotify(TITLE.NOTIFY, error);
      } finally {
      }
    },
    /**
     * @description:刷新
     * @param {type}
     * @return:
     * @author: CH
     */
    refresh() {
      if (this.active === 0) {
        this.$refs.warningTable.toggle(this.active2);
      } else {
        this.$refs.parameterAlarmTable.toggle(this.active2);
      }
    },
    /**
     * @description: 刷新表格数据
     * @param 无
     * @return void
     * @author: cyb
     */
    // refreshTableData(savedSearchCondition) {
    //   if (savedSearchCondition) {
    //     this.savedSearchCondition = [...savedSearchCondition];
    //     this.tablePage.currentPage = 1;
    //   }
    //   this.findTableList();
    // },
    /**
     * @description:处理查询数据
     * @param
     * @return:
     * @author: CH
     */
    findTable(conditionArr1, groupOperator, conditionArr2) {
      this.searchData = [];
      if (conditionArr1 !== undefined) {
        this.searchData.conditionArr1 = JSON.parse(
          JSON.stringify(conditionArr1)
        );
        for (var i in this.searchData.conditionArr1) {
          this.searchData.conditionArr1[
            i
          ].value = this.searchData.conditionArr1[i].value.toString();
        }
      }
      if (groupOperator !== undefined) {
        this.searchData.groupOperator = groupOperator;
      }
      if (conditionArr2 !== undefined) {
        this.searchData.conditionArr2 = JSON.parse(
          JSON.stringify(conditionArr2)
        );
        for (var x in this.searchData.conditionArr2) {
          this.searchData.conditionArr2[
            x
          ].value = this.searchData.conditionArr2[x].value.toString();
        }
      }
      if (this.active === 0) {
        this.$refs.warningTable.search(this.searchData);
      } else {
        this.$refs.parameterAlarmTable.search(this.searchData);
      }
      // this.onTablePageChanges(1, this.tablePages.pageSize);
    }
  },
  components: {
    // 加载未审核组件
    HeaderTitle,
    warningTable,
    warningInfo,
    parameterAlarmTable,
    parameterAlarmInfo,
    HeadOperationItem,
    SearchBox
  }
};
